@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans:400,700&display=swap);
body {
  position: relative;
  margin: 0;
  font-family: 'Alegreya Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;

}


.App{text-align:center;width:100%;background-color:#282c34;background-image:url(/music-tabs/static/media/bg.93012910.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white;-webkit-transition:0.3s width;transition:0.3s width;overflow:hidden}form{display:flex}label{display:flex}.search-tool__title{letter-spacing:10px}.search-box{display:flex;align-items:center;justify-content:center}.search-box select,.search-box input{background-color:#282c34;border:2px solid #6a6a6b;border-radius:5px;padding:7px;color:white;opacity:0.8;letter-spacing:4px}.search-box select,.search-box input:focus{outline:none}.search-box option:active,.search-box select:active,.search-box input:active{background-color:#676768}.search-box select:hover,.search-box input:hover{background-color:#676768}.search-type{margin:0 10px 0 10px}.search-type__text{margin:0 20px 0 20px;letter-spacing:5px;width:500px}.search-tool_error{color:red}.search-tool_error__hide{display:none}@media (max-width: 900px){form{flex-direction:column;align-items:center}.search-type{margin-top:50px}.search-type__text{max-width:300px;font-size:12px}.search-tool__title{letter-spacing:5px;font-size:16px;width:300px}.wrapper{width:500px;display:flex;flex-direction:column;align-items:center}}.search-result{position:absolute;top:0;right:0;background-color:#14191c;width:400px;max-height:100vh;-webkit-transform:translateX(100%);transform:translateX(100%);-webkit-transition:0.3s -webkit-transform;transition:0.3s -webkit-transform;transition:0.3s transform;transition:0.3s transform, 0.3s -webkit-transform;font-size:calc(10px + 1vmin);text-align:center;overflow:scroll;overflow-x:hidden}.search-result li{list-style:none;display:flex;align-items:center;justify-content:center;border-left:5px solid #14191c}.search-result li:hover{opacity:0.6;font-weight:bold}.search-result li:nth-child(odd){background-color:#1d2122}.search-result a{color:#fff;text-decoration:none;cursor:pointer;display:block;width:300px;height:auto;padding:15px;letter-spacing:3px}.search-result_block{-webkit-transform:translateX(0);transform:translateX(0)}

